.header-color{
  background: #833ab4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.Landing-grid{
  background: #833ab4;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
text-align: center;
/* position: fixed; */
/* top: 0; */
left: 0;
width: 100%;
height: 100%;

}

        /* project css */
.projects-grid{
  display: flex;
}

        /* Landing page/Home page css */
.avatar-img{
  height: 200px;
  width: 200px;
  /* padding-top: 5em; */
  border-radius: 200px;
}
.banner-text{
  background-color: black;
  opacity: 8;
  width: 75%;
  margin:auto;
  border-radius: 10px;
}
.banner-text h1{
  font-size: 55px;
  font-weight: bold;
  color: white;
}
.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}
.banner-text p{
  color: white;
  font-size: 20px;
  padding: 1em;
}
.social-link{
  display: flex;
  /* justify-content: space-between; */
  justify-content: center;
  width: 30%;
  margin:auto;
}
.social-link i{
  color: white;
  font-size: 3em;
  padding-left: 1.5rem;
}


          /* contact page css */
.contact-body{
  margin: auto;
  /* background: grey; */
  /* position: fixed; */
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;

}
.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
 
  margin-bottom: 20px;
}
.contact-frid h2{
  font-family: 'Anton', sans-serif;
}
.contact-grid p{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}
.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}
.contact-list i{
 font-size: 26px;
 padding-right: 1rem;
}
                  /* Resume css */
.resume-right-col{
  background: #27221f;
  color: white;
  padding: 2em;
}                  